import React, { PureComponent } from 'react'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

const Footer = () => {
  return (
    <Grid container={true} alignContent='center' alignItems='center' justify='center' className='silver bg-dark-gray pv4'>
      <Grid item={true}>
        <Typography color='inherit'> 2018 Codemitter Technology Pvt. Ltd.</Typography>
      </Grid>
    </Grid>
  )
}

export default Footer
