import React from 'react'
import { Mutation } from 'react-apollo'

export default ({ children, ...props }: any) => {
  return (
    <Mutation {...props}>
      {(mutate, lprops) => {
        return children(mutate, { ...lprops, error: { message: lprops.error && lprops.error.message.replace('GraphQL error: ', '') } })
      }}
    </Mutation>
  )
}
