import React, { PureComponent } from 'react'
import PhoneIcon from '@material-ui/icons/Phone'
import InputIcon from '@material-ui/icons/Input'
import { withStyles, createStyles, WithStyles } from '@material-ui/core/styles'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import { Link } from 'react-router-dom'
import Avatar from '@material-ui/core/Avatar'
import Menu from '@material-ui/core/Menu'
import ChangeContact from './Dialogs/ChangeContact'
import ChangePassword from './Dialogs/ChangePassword'
import VpnKey from '@material-ui/icons/VpnKey'
import DrawerMenu from './DrawerMenu'
import filter from '../utils/filter'
import { CreateMenuItem } from './EnhancedMenu'
import { Theme } from '@material-ui/core/styles/createMuiTheme'

const styles = (theme: Theme) => createStyles({
  root: {
    flexGrow: 1,
  },
  flex: {
    flex: 1,
  },
  orangeAvatar: {
    margin: 10,
    backgroundColor: '#fff',
  },
  menuButton: {
    marginLeft: -12,
    marginRight: 20,
  },
})

const APP_NAME = 'Real Estate'

interface IHeaderProps extends WithStyles<typeof styles> {
  client: any,
  user: any,
  pageTitle?: string
}
interface IHeaderState {
  anchorEl: null,
  dialog: boolean,
}
class Header extends PureComponent<IHeaderProps, IHeaderState> {
  state = {
    anchorEl: null,
    dialog: false,
  }

  handleClick = (event: any) => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleClose = () => {
    this.setState({ anchorEl: null })
  }
  render() {
    const { classes, client, pageTitle = APP_NAME, ...restProps } = this.props
    const { user } = this.props
    const { anchorEl } = this.state
    return (
      <header>
        <AppBar>
          <Toolbar>
            {user && user.isAdmin() && <DrawerMenu {...this.props} title={pageTitle || APP_NAME} />}
            <Typography variant='title' color='inherit' className={classes.flex} component={Link as any} {...{ to: '/' }}>
              {pageTitle}
            </Typography>
            <Avatar className={classes.orangeAvatar}>
              <Button
                color='primary'
                aria-owns={anchorEl ? 'simple-menu' : undefined}
                aria-haspopup='true'
                onClick={this.handleClick}
              >
                {(user && user.username[0]) || '-'}
              </Button>
            </Avatar>
            <Menu
              id='simple-menu'
              anchorEl={anchorEl}
              open={Boolean(anchorEl)}
              onClose={this.handleClose}
            >
              <ChangeContact
                {...restProps}
                positiveHandler={(fn, args) => {
                  return fn({
                    variables: {
                      id: user.id,
                      ...args,
                    },
                  })
                }}>
                <CreateMenuItem
                  icon={<PhoneIcon />}
                  content='Change Contact'
                />
              </ChangeContact>
              <ChangePassword
                {...restProps}
                positiveHandler={(fn, args) => {
                  return fn({
                    variables: {
                      id: user.id,
                      data: filter(args, ['confirmNewPassword']),
                    },
                  })
                }}
              >
                <CreateMenuItem
                  icon={<VpnKey />}
                  content='Change password'
                />
              </ChangePassword>
              <CreateMenuItem
                {...{
                  onClick: async () => {
                    this.handleClose()
                    await window.localStorage.clear()
                    if ('serviceWorker' in navigator) {
                      const keys = await caches.keys()
                      keys.forEach(function(cacheName) {
                        caches.delete(cacheName)
                      })
                    }
                    client.resetStore()
                    window.location.href = '/'
                  },
                }}
                icon={<InputIcon />}
                content='Logout'
              />
            </Menu>
          </Toolbar>
        </AppBar>
      </header>
    )
  }
}

export default withStyles(styles)(Header)
