import React from 'react'
import { asField } from 'informed'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Checkbox from '@material-ui/core/Checkbox'
import FormHelperText from '@material-ui/core/FormHelperText'

export default asField(({ fieldState, fieldApi, ...props }: any) => {
    const {
        value,
        error,
    } = fieldState
    const {
        setValue,
        setTouched,
    } = fieldApi
    const {
        onChange,
        onBlur,
        forwardedRef,
        field: name,
        ...rest
    } = props
    return (
        <FormControlLabel
            className='w-100 mt3 ttc'
            control={
                <React.Fragment>
                    <Checkbox
                        name={name}
                        {...rest}
                        ref={forwardedRef}
                        checked={value === true}
                        value={String(value)}
                        onChange={e => {
                            setValue(e.target.value === 'false')
                            if (onChange) {
                                onChange(e)
                            }
                        }}
                        onBlur={e => {
                            setTouched()
                            if (onBlur) {
                                onBlur(e)
                            }
                        }}
                    />
                    {
                        error ? <FormHelperText error={true}>{error}</FormHelperText> : null}
                </React.Fragment>
            }
            label={name}
        />
    )
})
