import React from 'react'
import { asField } from 'informed'
import { withStyles } from '@material-ui/core/styles'
import FormControl from '@material-ui/core/FormControl'
import Input from '@material-ui/core/Input'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'

export default asField(({ fieldState, fieldApi, ...props }: any) => {
  const {
    value,
    error,
  } = fieldState
  const {
    setValue,
    setTouched,
  } = fieldApi
  const {
    onChange,
    onBlur,
    forwardedRef,
    field: name,
    label = name,
    options,
    selected = null,
    mapping,
    ...rest
  } = props
  console.log(value, typeof value)
  return (
    <FormControl fullWidth={true} className='mt3'>
      <InputLabel htmlFor={name} className='ttc'>{label}</InputLabel>
      <Select
        {...rest}
        ref={forwardedRef}
        value={value || ''}
        onChange={e => {
          console.log(value, typeof value, e.target.value)
          setValue(e.target.value)
          if (onChange) {
            onChange(e)
          }
        }}
        onBlur={e => {
          setTouched()
          if (onBlur) {
            onBlur(e)
          }
        }}
        inputProps={{
          name,
        }}
      // input={<Input name={name} />}
      >
        {/* <MenuItem value="" selected>
                    <em>None</em>
                </MenuItem> */}
        {options.map((e: any) => {
          return mapping ? (
            <MenuItem value={e[mapping.value]} key={e[mapping.key]}>{e[mapping.key]}</MenuItem>
          ) : (
              <MenuItem value={e} key={e}>{e}</MenuItem>
            )
        })}
      </Select>
      {
        error ? <FormHelperText error={true}>{error}</FormHelperText> : null}
    </FormControl>
  )
})
