import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles'
import DeleteIcon from '@material-ui/icons/Delete'
import HomeIcon from '@material-ui/icons/Home'
import LocalGroceryStoreIcon from '@material-ui/icons/LocalGroceryStore'
import TimelineIcon from '@material-ui/icons/Timeline'
import React from 'react'
import { Routes } from '../Router'
// import Typography from '@material-ui/core/Typography';

const listStyles = (theme: Theme) => createStyles({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
})
interface ISimpleListProps extends WithStyles<typeof listStyles> {
  title: string,
  history?: any,
}
const SimpleList = withStyles(listStyles)(function(props: ISimpleListProps) {
  const { classes } = props
  const { title = '', history } = props
  return (
    <div className={classes.root}>
      <List component='nav'>
        <ListItem>
          <ListItemText primary={title} />
        </ListItem>
        <ListItem button={true} onClick={e => history.push(Routes.index())}>
          <ListItemIcon>
            <HomeIcon />
          </ListItemIcon>
          <ListItemText primary='Dashboard' />
        </ListItem>
        <ListItem button={true} onClick={e => history.push(Routes.Stock.index())}>
          <ListItemIcon>
            <LocalGroceryStoreIcon />
          </ListItemIcon>
          <ListItemText primary='Stocks' />
        </ListItem>
        <ListItem button={true} onClick={e => history.push(Routes.Activity.index())}>
          <ListItemIcon>
            <TimelineIcon />
          </ListItemIcon>
          <ListItemText primary='Activities' />
        </ListItem>
        {/* <ListItem button>
          <ListItemIcon>
            <DeleteIcon />
          </ListItemIcon>
          <ListItemText primary="Trash" />
        </ListItem> */}
      </List>
    </div>
  )
})

export default SimpleList
