import React from 'react'
import { asField } from 'informed'
import FormControl from '@material-ui/core/FormControl'
import Input from '@material-ui/core/Input'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import { string } from 'prop-types'

export default asField(({ fieldState, fieldApi, ...props }: any) => {
  const {
    value,
    error,
  } = fieldState
  const {
    setValue,
    setTouched,
  } = fieldApi
  const {
    onChange,
    onBlur,
    forwardedRef,
    field: name,
    label = name,
    filter = (val: any) => val,
    ...rest
  } = props
  return (
    <FormControl fullWidth={true} className='mt3'>
      <InputLabel htmlFor={name} className='ttc'>{label}</InputLabel>
      <Input name={name}
        {...rest}
        ref={forwardedRef}
        value={!value && value !== 0 ? '' : value}
        onChange={e => {
          // const value = filter(e.target.value)
          // if(value instanceof string){}
          setValue(filter(e.target.value))
          if (onChange) {
            onChange(e)
          }
        }}
        onBlur={e => {
          setTouched()
          if (onBlur) {
            onBlur(e)
          }
        }}
      >
      </Input>
      {
        error ? <FormHelperText error={true}>{error}</FormHelperText> : null}
    </FormControl>
  )
})
