import React, { Component } from 'react'

import Query from './Query'
import { LOCAL_LOGIN } from '../graphql/query/local'
import Header from './Header'
import Body from './Body'
import Footer from './Footer'
import parseJWT from '../utils/parseJWT'

class Layout extends Component {
  render() {

    return (
      <Query query={LOCAL_LOGIN}>
        {
          ({ client, data }: IGraphqlData) => {
            let user: any = null
            if (data.localLogin && data.localLogin.token) {
              user = parseJWT(data.localLogin.token)
              user.isAdmin = () => Number(user.role) === 1
              user.isManager = () => Number(user.role) === 0
            }
            return (
              <React.Fragment>
                <Header {...this.props} client={client} user={user} />
                <Body>
                  {this.props.children}
                </Body>
                <Footer></Footer>
              </React.Fragment>
            )
          }}
      </Query>
    )
  }
}

export default Layout
